.wrapper {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100%;
	height: 100vh;
}

.wrapper.error {
	background-color: red;
}

.input {
	margin: 10px;
	width: 300px;
	height: 80px;
	font-size: 20px;
	display: block;
	text-align: center;
}

.input.error {
	background-color: #B22222;
	color: #ffffff;
}